* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background: #333;
}

.container {
  width:75%;
	margin:auto;
	overflow: hidden;
}

a {
  text-decoration: none;
}

/*
  Media querries: 
    0-400px - small phone
    400px-600px - phone
    600-900px - tablet portrait mode
    900-1200px - tablet landscape
    1200-1800px - desktop
    1800+px - big desktop 
*/

@media (max-width: 600px){

  .bars {
    margin-top: -1rem;
    margin-right: .5rem;
    font-size: 1rem;
    padding: .7rem;
  }

  .navLinks {
    display: none;
  }

}
footer {
    padding: 4rem 0;
    display: grid;
    grid-gap: 2.5rem;
    grid-template-columns: 1fr 1fr 1fr;
    border-top: .5rem solid #ccc;
}

@media (max-width: 900px) {
    footer {
        display: block;
        padding-bottom: 0;
        padding-top: 2rem;
    }

    footer .column {
        margin-bottom: 2rem;
    }
}
.heroHomeMain {
    color: #eee;
    font-size: 3.5rem;
    padding-top: 20rem;
    font-weight: 300;
}

.heroHomeSecondary {
    color: #dccc;
    font-size: 2.3rem;
    letter-spacing: 5.6px;
}

.heroHomeButton {
    position: absolute;
    top: 90%;
    font-size: 1.5rem;
    cursor: pointer;
}

@media (max-width: 600px){
    .heroHomeMain {
        font-size: 2rem;
        padding-top: 24rem;
    }

    .heroHomeSecondary {
        font-size: 1.4rem;
        letter-spacing: 2.5px;
    }

    .heroHomeButton {
        font-size: 1.2rem;
    }
}
.mainContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 50vh;
}

.mainContainer .boring {
    width: 75%;
    margin-left: auto;
    margin-right: 0;
}

.mainContainer .unboring {
    width: 75%;
    margin-left: 0;
    margin-right: auto;
    text-align: right;
}

.mainContainer .boring h1 {
    color: #333;
    margin: 2rem 0;
    font-size: 2.4rem;
}

.mainContainer .unboring h1 {
    color: #ccc;
    margin: 2rem 0;
    font-size: 2.4rem;
}

.mainContainer .boring h2 {
    color: #333;
    margin-Bottom: 2rem;
    padding-right: 3.8rem;
    font-weight: 400;
    font-size: 1.4rem;
}

.mainContainer .unboring h2 {
    color: #ccc;
    margin-Bottom: 2rem;
    padding-left: 3.8rem;
    font-weight: 400;
    font-size: 1.4rem;
}

@media (max-width: 1200px) {
    .mainContainer {
        height: auto;
    }

    .mainContainer .boring h1 {
        font-size: 1.7rem;
    }

    .mainContainer .unboring h1 {
        font-size: 1.7rem;
    }
}

@media (max-width: 900px) {
    .mainContainer {
        height: auto;
    }

    .mainContainer .boring h1{
        margin: 2rem 2rem 2rem 0;
        font-size: 1.8rem;
    }

    .mainContainer .unboring h1 {
        margin: 2rem 0 2rem 2rem;
        font-size: 1.8rem;
    }

    .mainContainer .boring h2{
        font-size: 1.3rem;
    }

    .mainContainer .unboring h2{
        font-size: 1.3rem;
    }
}

@media (max-width: 600px) {
    .mainContainer {
        display: block;
        height: auto;
    }

    .mainContainer .boring {
        margin: auto;
    }

    .mainContainer .unboring {
        margin: auto;
    }

    .mainContainer .boring h1 {
        font-size: 1.6rem;
        margin-top: 2rem;
        margin-bottom: 1rem;
        display: inline-block;
    }

    .mainContainer .unboring h1 {
        font-size: 1.6rem;
        margin-top: 2rem;
        margin-bottom: 1rem;
        display: inline-block;
    }

    .mainContainer .boring h2 {
        padding: 0;
        font-size: 1.2rem;
    }

    .mainContainer .unboring h2 {
        padding: 0;
        font-size: 1.2rem;
    }
}
.projectsHome {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 50vh;
}

.pro {
    color: #ccc;
    margin: 4rem 0;
    font-size: 4rem;
}

.ject {
    color: #333;
    margin: 4rem 0;
    font-size: 4rem; 
}

.buttonLeft {
        color: #ccc;
        border: 2px solid #ccc;
        padding: 1rem .25rem 1rem 2rem;
        border-right: none;
        font-size: 1.6rem;
        font-weight: 600;
        display: inline-block;
        margin-bottom: 4rem;
}

.buttonRight {
    color: #333;
    border: 2px solid #333;
    padding: 1rem 2rem 1rem .25rem;
    border-left: none;
    font-size: 1.6rem;
    font-weight: 600;
    display: inline-block;
    margin-bottom: 4rem;
}

@media (max-width: 1200px){
    .projectsHome {
        height: auto;
    }
}

@media (max-width: 600px){
    .projectsHome {
        height: auto;
    }

    .pro {
        font-size: 3.2rem;
    }

    .ject {
        font-size: 3.2rem;
    }

    .buttonLeft {
        font-size: 1.4rem;
    }

    .buttonRight {
        font-size: 1.4rem;
    }
}
.containerMainAbout {
    background: #ccc;
    height: 100vh;
}

.aboutHeading {
    font-size: 3.5rem;
    border-left: 3px solid #333;
    padding: 3rem;
}

.headingAboutDiv {
    height: 30vh;
    background: #222;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.aboutTextPicture {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 70vh;
}

.aboutTextPicture p {
    margin: 0 2.5rem 2rem 0;
    font-size: 1.3rem;
    color: #222;
    font-weight: 500;
    text-align: justify;
}

.aboutTextPicture .left {
    height: 58vh;
    margin: auto 2.5rem auto 0;
}

.aboutTextPicture .right {
    background: url(/PortfolioWebsite/static/media/aboutMe.d8ccde34.jpg);
    background-position: 50% 75%;
    background-size: cover;
    height: 58vh;
    margin: auto 0 auto 2.5rem;
}

@media (max-width: 1200px) {

    .containerMainAbout {
        height: auto;
    }

    .headingAboutDiv {
        height: auto;
    }

    .aboutTextPicture {
        height: 40vh;
    }

    .aboutTextPicture .left {
        height: auto;
        margin: 1.5rem 0;
    }

    .aboutTextPicture .right {
        height: 35vh;
    }

    .aboutTextPicture p {
        margin: 0;
        margin-bottom: 1.5rem;
        text-align: left;
    }
}

@media (max-width: 900px){
    .aboutTextPicture .right {
        display: none;
    }

    .aboutTextPicture {
        display: block;
        height: auto;
    }
}

@media (max-width: 600px){
    .aboutHeading{
        font-size: 1.5rem;
        padding: 1.5rem;
    }
}
.headingAboutPage{
    color: #ccc;
    font-size: 2.9em;
    font-weight: 300;
}

.headingSecondaryAboutPage {
    color: #dccc;
    font-size: 1.8rem;
    letter-spacing: 5.6px;
    margin-top: -.5rem;
}

.listAbout {
    font-size: 1.3rem;
    color: #eee;
}

@media (max-width: 900px){
    .listAbout{
        font-size: 1rem;
    }
}

@media (max-width: 600px){
    .headingAboutPage {
        font-size: 1.7rem;
    }

    .headingSecondaryAboutPage {
        font-size: 1.2rem;
        letter-spacing: 2px;
        margin-top: -.2rem;
    }

    .listAbout {
        font-size: .9rem;
    }

    .contactInfoAbout {
        display: none;
    }
}
.interestsGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.5rem;
}

@media (max-width: 600px){
    .interestsGrid{
        display: inline-block;
    }
}
.skillsPageDiv {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 75vh;
    margin-top: -10vh;
}

.skillsPageDiv .left {
    width: 75%;
    margin-left: auto;
    margin-right: 0;
    text-align: left;
}

.skillsPageDiv .right {
    width: 75%;
    margin-left: 0;
    margin-right: auto;
    text-align: right;
}

@media (max-width: 600px) {
    .skillsPageDiv {
        display: block;
        height: auto;
    }

    .skillsPageDiv .left {
        margin: -8rem auto -12rem auto;
    }

    .skillsPageDiv .right {
        margin: auto;
    }
}
.boringHeading {
    color: #222;
    background-image: linear-gradient(to right, rgba(204, 204, 204, .8), rgba(204, 204, 204, .4));
    padding: .75rem 3rem;
    font-size: 3.2em;
    display: inline-block;
    margin-top: 8rem;
    border-radius: 10px;
}

@media (max-width: 600px){
    .boringHeading {
        font-size: 2rem;
        width: 75%;
        padding: .75rem 1.25rem;
        text-align: center;
    }
}
.boringDiv {
    padding-top: 3rem;
    padding-bottom: 3rem;
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 4rem;
}

.subheadings{
    font-size: 2rem;
    color: #333;
    margin: 1rem 0;
}

.skillsList {
    font-size: 1.5rem;
    margin-left: 2.5rem;
    color: #333;
    line-height: 1.5;
}

@media (max-width: 900px) {
    .boringDiv {
        display: block;
    }

    .subheadings {
        font-size: 1.6rem;
    }

    .skillsList{
        font-size: 1.2rem;
    }
}
.unboringHeading {
    color: #222;
    background-image: linear-gradient(to right, rgba(204, 204, 204, .8), rgba(204, 204, 204, .4));
    padding: .75rem 3rem;
    font-size: 3.2em;
    display: inline-block;
    margin-top: 8rem;
    border-radius: 10px;
}

@media (max-width: 900px){
    .unboringHeading {
        font-size: 2.5rem;
    }
}

@media (max-width: 600px){
    .unboringHeading {
        font-size: 1.6rem;
        width: 75%;
        padding: .75rem 1.25rem;
        text-align: center;
    }
}
.unboringDiv {
    padding-top: 3rem;
    padding-bottom: 3rem;
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 4rem;
}

.subheadings{
    font-size: 2rem;
    color: #333;
    margin: 1rem 0;
}

.skillsList {
    font-size: 1.5rem;
    margin-left: 2.5rem;
    color: #333;
    line-height: 1.5;
}

@media (max-width: 900px) {
    .unboringDiv {
        display: block;
    }

    .subheadings {
        font-size: 1.6rem;
    }

    .skillsList{
        font-size: 1.2rem;
    }
}
.projectsGrid {
    display: grid;
    grid-gap: 1.5rem;
    grid-template-columns: 1fr 1fr 1fr;
}

.description {
    margin: 1rem 1.5rem 1rem .75rem;
    color: #222;
    font-weight: 600;
    height: 10vh;
}

.languages {
    display: inline;
    margin-right: .5rem;
    padding: .5rem .75rem;
    background: #222;
    border-radius: 12px;
    color: #ddd;
}

@media (max-width: 900px){
    .projectsGrid {
        display: block;
    }

    .description {
        height: 6vh;
    }
}

@media (max-width: 600px){
    .projectsGrid {
        display: block;
    }

    .description {
        height: 10vh;
    }

    .languages {
        display: inline-block;
        margin-bottom: .5rem;
    }
}

@media (max-width: 400px){
    .description {
        height: 15vh;
    }
}
